import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/authservice/authservice';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  public userDetailcuit:any;
  public userDetailtoken:any;
  public vencimientos: any=[];

  constructor(public navCtrl: NavController,  public auth: AuthServiceProvider) {
    const datacuit = localStorage.getItem("userDatacuit");
    this.userDetailcuit = datacuit;
    const datatoken = localStorage.getItem("userDatatoken");
    this.userDetailtoken = datatoken;
    console.log("User Cuit: " + this.userDetailcuit);
    console.log("User Token: " + this.userDetailtoken);
    this.getVenci();
    
  }

  
  getVencimientos(){
    this.auth.getDatos(this.userDetailcuit, this.userDetailtoken).subscribe(
      data => this.vencimientos = data,
      err => console.log(err)
    );

    console.log(this.vencimientos);
  }

  getVenci(){
    this.auth.get(this.userDetailcuit, this.userDetailtoken).subscribe((data)=>{
      this.vencimientos = data;
    });
  }


  
}



