import { Http, Headers} from '@angular/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

/*
  Auth - Service, sirve para la identificaci칩n y traer datos de la api externa.


*/
@Injectable()
export class AuthServiceProvider {
  apiUrl = "http://ubik.com.ar/";
  public Vencimientos: any = [];
  constructor(public http: Http) {
    console.log('Hello AuthServiceProvider Provider');
  }

  get(user:string='', token:string=''){
    return this.http.get(this.apiUrl + user + "/"+ token).map(res=>res.json());
    
  }


  postData(credentials){
    return new Promise((resolve, reject) =>{
      let headers = new Headers();
      this.http.post(this.apiUrl, JSON.stringify(credentials), {headers: headers}).
      subscribe(res =>{
        resolve(res.json());
      }, (err) =>{
        reject(err);
      });
    });
  }

  getDatos(user, token){
    return this.http.get(this.apiUrl+  user + "/"+ token).map(res=>res.json());
  }

  getVencimientos(user, token){
    return this.get(this.apiUrl+  user + "/"+ token).subscribe((data)=>{
        this.Vencimientos= data;
    })
  }
}



