import { Component } from '@angular/core';

import { NavController, ToastController } from 'ionic-angular';
import { TabsPage } from '../tabs/tabs';
import {AuthServiceProvider} from '../../providers/authservice/authservice';
import { HomePage } from '../home/home';


@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  resposeData : any;
  userData = {"cuit":"", "token":""};
  constructor(public navCtrl: NavController, public authService: AuthServiceProvider,private toastCtrl:ToastController) {

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  login(){
    console.log("Entre al login metodo");
    if(this.userData.cuit && this.userData.token){
     console.log("Verifico si cuit y token son true");
     console.log(this.userData);
     if(this.userData.cuit && this.userData.token){
        localStorage.setItem('userDatacuit',this.userData.cuit );
        localStorage.setItem('userDatatoken',this.userData.token );

        this.navCtrl.push(TabsPage);
      }
  }

  


  }


  entrar(){
    if(this.userData.cuit && this.userData.token){
     this.authService.postData(this.userData).then((result) =>{
     this.resposeData = result;
     console.log("user" + this.resposeData);
     if(this.resposeData.userData){
      localStorage.setItem('userData', JSON.stringify(this.resposeData) )
      this.navCtrl.push(TabsPage);
      console.log("Userdata del login : "+this.userData);
   }
   else{
     this.presentToast("El email o contrase침a es incorrecto!");
   }
     }, (err) => {
       //Connection failed message
       this.presentToast("No hay conexi칩n con el servidor!");
     });
    }
    else{
     this.presentToast("Tienes que ingresar un email y una contrase침a");
    }

 }

 presentToast(msg) {
  let toast = this.toastCtrl.create({
    message: msg,
    duration: 2000
  });
  toast.present();
}

  atras(){
    this.navCtrl.push(HomePage);
  }
}



